
    import { defineComponent } from "vue";

    const arraySelector = defineComponent({
        props: {
            originalArray: Array
        },
        data() {
            return {
                checkedStrings: new Array<string>()
            }
        },
        emits: ['arrayChanged'],
        watch: {
            checkedStrings(newValue, oldValue) {
                this.$emit('arrayChanged', newValue);
            }
        }
    });

    export default arraySelector;
