
    import { defineComponent } from "vue";


    const searchElement = defineComponent({
        props: {
            name: String,
            group: String,
            direction: String,
            specialization: String,
            course: String,
            educationForm: String
        },
        methods: {
            
        },
        computed: {
            degree: function (): string {
                let d = this.direction;
                let res = "";
                if (d?.indexOf(".02.") != -1) {
                    res = "СПО"
                }
                if (d?.indexOf(".03.") != -1) {
                    res = "Бакалавриат"
                }
                if (d?.indexOf(".04.") != -1) {
                    res = "Магистратура"
                }
                if (d?.indexOf(".05.") != -1) {
                    res = "Специалитет"
                }
                if (d?.indexOf(".06.") != -1) {
                    res = "Аспирантура"
                }
                if (res == "") {
                    if (d?.indexOf("03") != -1) {
                        res = "Бакалавриат"
                    }
                    if (d?.indexOf("04") != -1) {
                        res = "Магистратура"
                    }
                    if (d?.indexOf("05") != -1) {
                        res = "Специалитет"
                    }
                    if (d?.indexOf("06") != -1) {
                        res = "Аспирантура"
                    }
                }
                return res;
            }
        }
    });

    export default searchElement;
